window.addEventListener('load', function () {
    const tab = (btnSel, bodySel) => {
        const tabBtn = document.querySelectorAll(btnSel);
        const tabBody = document.querySelectorAll(bodySel);

        for (let i = 0; i < tabBtn.length; i++) {

            tabBtn[i].addEventListener('click', () => {
    
                for (let x = 0; x < tabBtn.length; x++) {
                    tabBtn[x].classList.remove('active_tab');
                    tabBody[x].classList.remove('active_tab_body');
                }
    
                tabBtn[i].classList.add('active_tab');
                tabBody[i].classList.add('active_tab_body');
            });
        };
    };

    const setClass = (setBtnSel, remBtnSel, itemSel, cls) => {

        const setBtn = document.querySelector(setBtnSel);
        const remBtn = document.querySelector(remBtnSel);
        const item = document.querySelector(itemSel);

        setBtn.addEventListener('click', () => {
            item.classList.add(cls);
        });

        remBtn.addEventListener('click', () => {
            item.classList.remove(cls);
        });
    };

    const toggleSub = (btnSel, itemSel) => {
        const btn = document.querySelector(btnSel);
        const item = document.querySelector(itemSel);

        let tick;

        btn.addEventListener('click', () => {
            item.style.display = 'block';
            clearInterval(tick);
        });

        btn.addEventListener('mouseleave', () => {
            tick = setTimeout(() => {
                item.style.display = 'none'
            }, 1200);
        });

        item.addEventListener('mouseover', () => {
            item.style.display = 'block';
            clearInterval(tick);
        });

        item.addEventListener('mouseleave', () => {
            item.style.display = 'none';
            clearInterval(tick);
        });

        return btn, item;
    };

    // filter tab
    tab('.tab_btn', '.tab_body');
    tab('.home_tab_btn', '.home_tab_body');

    // submenu - services
    toggleSub('#submenu_toggle', '#submenu');
    toggleSub('#submenu', '#submenu');

    // languages dropdown
    toggleSub('.current_lang', '.languages');
    toggleSub('.languages', '.languages');

    // mobile menu
    setClass('.mobileMenuBtn', '.closeMobileMenu', '.wrapper', 'active_wrapper');


    // slider

    let swiper = new Swiper('.swiper-container', {
        slidesPerView:4,
        spaceBetween: 30,
        loop: true,
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        },
        breakpoints: {
            640: {
              slidesPerView: 1
            },
            768: {
              slidesPerView: 2
            },
            1024: {
              slidesPerView: 3
            }
        }
    });
});